@keyframes open {
    from {
      transform: scale(0);
    }
    to {
      transform: scale(1);
    }
}
  
.edit-asset-background {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 0;
}

.edit-asset-popup {
    position: fixed;
    display: flex;
    flex-direction: column;
    max-height: 80vh;
    overflow: auto;
    /*height: auto;*/
    width: 32rem;
    background-color: var(--border-color);
    border: 1px solid white;
    color: black;
    box-shadow: 3px 3px 6px rgba(0,0,0,0.8);
    top: var(--popup-top-offset);
    left: var(--popup-left-offset);
    margin-left: -16rem;
    animation: open 0.5s;
}

.edit-asset-popup-title {
    display: flex;
    font-size: 1.6rem;
    padding: 1.4rem 0 1.2rem 1.2rem;
    border-bottom: 0.1rem solid white;
}

.edit-asset-popup-title-icon {
    margin-right: 1rem;
}

.edit-asset-popup-description {
    font-size: 1.2rem;
    padding: 1.2rem;
    line-height: 1.6rem;
}

.edit-asset-menu-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    user-select: none;
    margin: 0 1.2rem;
}

.edit-asset-menu-item {
    margin-bottom: 1rem;
    font-size: 1.4rem;
    font-weight: bold;
    font-variant: small-caps;
    letter-spacing: 0.1rem;
    list-style: none;
    color: var(--primary-color);
}

.edit-asset-menu-item-active {
    color: black;
    border-bottom: 0.1rem solid black;
}

.edit-asset-menu-item:hover {
    color: black;
    cursor: pointer;
}

.edit-asset-input-container {
    display: flex;
    flex-direction: column;
    padding-bottom: 1rem;
    border-bottom: 0.1rem solid white;
}

.edit-asset-input-description {
    padding: 1.2rem
}

.edit-asset-input-description-title {
    font-size: 1.4rem;
    margin-bottom: 0.6rem;
}

.edit-asset-input-description-text {
    font-size: 1.2rem;
    line-height: 1.6rem;
}

.edit-asset-input {
    display: flex;
    flex-direction: column;
}

.edit-asset-input-item {
    display: grid;
    grid-template-columns: 0.8fr 2fr;
    align-items: center;
    padding: 0rem 0rem 1rem 1.2rem;
}

.edit-asset-input-label {
    font-size: 1.4rem;
    padding-right: 1rem;
}

.edit-asset-input-field {
    font-size: 1.4rem;
    width: 20rem;
    padding: 0.6rem;
}

.edit-asset-input-field:focus {
    outline: none;
}

.edit-asset-asset-container {
    padding: 1rem 1rem 1rem 3rem;
}

.edit-asset-asset-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0rem 6rem 1rem 0rem;
}

.edit-asset-coin-text-container {
    display: flex;
    align-items: center;
    font-size: 1.4rem;
}

.edit-asset-coin-image {
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
}

.edit-asset-button-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 0.4rem;
    height: 4rem;
    padding: 0.4rem;
}

.edit-asset-button {
    font-size: 1.4rem;
    cursor: pointer;
}

.edit-asset-button:hover {
    background-color: var(--secondary-color);
}

.edit-asset-button:active {
    transform: translate(0.1rem, 0.1rem);
    box-shadow: none;
}

.edit-asset-button:focus {
    outline: none;
}

.error-text {
    color: rgb(140, 0, 0);
    text-align: center;
    font-size: 1.2rem;
}

.warning-text {
    color: rgb(140, 0, 0);
    margin-top: 1rem;
    text-align: center;
}

.warning-text-highlight {
    font-size: 1.4rem;
    font-weight: bold;
    line-height: 2rem;
}

.edit-asset-details-container {
    display: flex;
    flex-direction: column;
    margin-left: 1.2rem;
}

.edit-asset-details-item-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    margin-bottom: 0.4rem;
}

.edit-asset-details-name-container {
    display: flex;
    align-items: center;
    margin-bottom: 0.6rem;
}

.edit-asset-details-icon {
    width: 2.2rem;
    height: 2.2rem;
    margin-right: 1rem;
}

.edit-asset-details-name {
    font-size: 1.6rem;
}

.edit-asset-details-group-name {
    font-size: 1.3rem;
    margin: 0.8rem 0 0.5rem 0;
}

.edit-asset-details-label {
    font-size: 1.2rem;
}

.edit-asset-details-data {
    font-size: 1.2rem;
    text-align: end;
    margin-right: 2rem;
}

.edit-asset-details-transactions-header {
    display: grid;
    grid-template-columns: 0.5fr 1fr 1fr 0.5fr;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    text-align: center;
}

.edit-asset-details-transactions-container {
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
    margin-right: 1.2rem;
}

.edit-asset-details-transaction-item-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    border-bottom: 0.1rem solid var(--secondary-color);
}

.edit-asset-details-transaction-item-container:last-of-type {
    border-bottom: none;
}

.edit-asset-details-transaction-item {
    display: grid;
    grid-template-columns: 0.3fr 1fr 1fr 0.6fr;
    text-align: center;
    margin-bottom: 1rem;
}

.edit-asset-details-transaction-item-date {
    margin-bottom: 0.6rem;
}

.edit-asset-details-transaction-item-icon-container {
    display: flex;
    margin-left: 1rem;
    justify-content: center;
}

.edit-asset-details-transaction-item-icon {
    margin: 0 0.5rem;
    cursor: pointer;
}

.edit-asset-details-transaction-buysell {
    justify-self: flex-start;
}

.edit-asset-details-transaction-quantity, .edit-asset-details-transaction-price {
    justify-self: flex-end;
}